import { useState } from "react";

function App() {
  const [calc, setCalc] = useState("");
  const [result, setResult] = useState("");
  const ops = ['/', '*', '+', '-', '^', '&', '|', '.', 'GCD'];

  const updateCalc = value => {
    if(ops.includes(value) && calc === '' || ops.includes(value) && ops.includes(calc.slice(-1))) return;
    if(calc == '0') setCalc(value);
    else setCalc(calc + value);
  }

  const calculate = () => {
    if(calc.includes('GCD')){

      let n1 = "";
      let i = 0;
      while(calc[i] != 'G'){
        n1 += calc[i];
        i++;
      }
      i+=3;
      let n2 = "";
      while(i<calc.length){
        n2 += calc[i];
        i++;
      }
      let a = parseInt(n1), b = parseInt(n2);

      while(a > 0 && b > 0){
        if (a > b){
          a = a % b;
        }
        else{
          b = b % a;
        }
      }

      if(a == 0) setCalc(b.toString());
      else setCalc(a.toString()); 
    }
    else if(calc.includes('LCM')){

      let n1 = "";
      let i = 0;
      while(calc[i] != 'L'){
        n1 += calc[i];
        i++;
      }
      i+=3;
      let n2 = "";
      while(i<calc.length){
        n2 += calc[i];
        i++;
      }
      let a = parseInt(n1), b = parseInt(n2);
      let c = a*b;
      while(a > 0 && b > 0){
        if (a > b){
          a = a % b;
        }
        else{
          b = b % a;
        }
      }

      if(a == 0){
        c /= b;
        setCalc(c.toString())
      }
      else{
        c /= a;
        setCalc(c.toString());
      } 
    }
    else if(calc.includes('nm')){
      const multiply = (n, m) => {
        let nn = m;
        let ans = 1.0;
        while(nn){
          if(nn % 2 == 0){
            n = n * n;
            nn /= 2;
          }
          else{
            ans = n * ans;
            nn -= 1;
          }
        }
        return ans;
      }
      let n1 = "";
      let i = 0;
      while(calc[i] != 'n'){
        n1 += calc[i];
        i++;
      }
      i+=3;
      let n2 = "";
      while(i<calc.length){
        n2 += calc[i];
        i++;
      }
      let a = parseInt(n1), b = parseFloat(n2);
      let l = 1.0, h = b;
      let e = 1e-5;
      while((h - l) > e){
        let mid = (h + l) / 2.0;
        if(multiply(mid, a) < b) l = mid;
        else h = mid;
      }
      let ans = l.toPrecision(6);
      setCalc(ans.toString());
    }
    else setCalc(eval(calc).toString());
  }

  const deleteIt = () => {
    if(calc == '') return;
    const value = calc.slice(0, -1);
    setCalc(value);
  }

  const ac = () => {
    if(calc == '') return;
    const value = '';
    setCalc(value);
  }

  const is_prime = () => {
    if(calc == '') return;
    let n = parseInt(calc);
    let f = 1;
    for(let i=2;i*i<=n;i++){
      if(n % i === 0){
        f = 0;
        break;
      }
    }
    if(f === 1) setCalc("1");
    else setCalc("0");
  }

  return (
    <div className="App">
      <div className="calculator">
        <div className="display">
          {calc}
        </div>
        <div className="operators">
          <button onClick={() => updateCalc('/')}>/</button>
          <button onClick={() => updateCalc('*')}>*</button>
          <button onClick={() => updateCalc('+')}>+</button>
          <button onClick={() => updateCalc('-')}>-</button>
          <button onClick={() => updateCalc('^')}>XOR</button>
          <button onClick={() => updateCalc('&')}>AND</button>
          <button onClick={() => updateCalc('|')}>OR</button>
          <button onClick={() => updateCalc('%')}>%</button>
          <button onClick={() => updateCalc('GCD')}>GCD</button>
          <button onClick={() => updateCalc('LCM')}>LCM</button>
          <button onClick={is_prime}>IS_PRIME</button>
          <button onClick={() => updateCalc('nm')}>nm</button></div>
          <div className="extra">
          <button onClick={deleteIt}>DEL</button>
          <button onClick={ac}>AC</button>
          </div>
        
        <div className="digits">
          <button onClick={() => updateCalc('1')}>1</button>
          <button onClick={() => updateCalc('2')}>2</button>
          <button onClick={() => updateCalc('3')}>3</button>
          <button onClick={() => updateCalc('4')}>4</button>
          <button onClick={() => updateCalc('5')}>5</button>
          <button onClick={() => updateCalc('6')}>6</button>
          <button onClick={() => updateCalc('7')}>7</button>
          <button onClick={() => updateCalc('8')}>8</button>
          <button onClick={() => updateCalc('9')}>9</button>
          <button onClick={() => updateCalc('0')}>0</button>
          <button onClick={() => updateCalc('.')}>.</button>
          <button onClick={calculate}>=</button>   
        </div>
      </div>
    </div>
  );
}

export default App;
